<template>
  <div class="quarters">
    <!-- 搜索 -->
    <el-form :inline="true" :model="quarter" class="demo-form-inline">
      <el-form-item label="小区名称">
        <el-input v-model="quarter.name" placeholder="请输入小区名称"></el-input>
      </el-form-item>
      <el-form-item label="创建时间">
        <el-date-picker
          v-model="quarter.createTime"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="search">搜索</el-button>
      </el-form-item>
    </el-form>
        <!-- 操作 -->
    <div>
      <el-button type="primary" @click="addQuarter">新增小区</el-button>
    </div>
    <!-- 列表 -->
    <el-table
      :data="tableData"
      border
      style="width: 100%;margin-top: 25px;">
      <el-table-column
        prop="communityId"
        label-class-name="etc"
        label="ID"
        width="100">
      </el-table-column>
      <el-table-column
        prop="communityName"
        label-class-name="etc"
        label="名称"
        >
      </el-table-column>
      <el-table-column
        prop="communityAddress"
        label-class-name="etc"
        label="位置">
      </el-table-column>
      <el-table-column
        prop="communityUserCount"
        label-class-name="etc"
        label="用户数量">
      </el-table-column>
      <el-table-column
        prop="communityShopCount"
        label-class-name="etc"
        label="关联门店">
      </el-table-column>
      <el-table-column
        prop="createTime"
        label-class-name="etc"
        label="创建时间">
      </el-table-column>
      <el-table-column
        label="操作"
        label-class-name="etc"
        width="200">
        <template slot-scope="scope">
          <el-button @click="showStores(scope.row)" type="text" size="small">查看关联门店</el-button>
          <el-button @click="editQuarter(scope.row)" type="text" size="small">编辑小区</el-button>
          <el-button type="text" size="small" @click="del(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页器 -->
     <!-- 数据不够 total的值为dataCount -->
    <el-pagination
      small
      layout="prev, pager, next"
      :total="50" 
      @current-change="changePage"
      
      
      >
    </el-pagination>
    <!-- 新增小区对话框 -->
    <el-dialog :title="title?'编辑小区':'新增小区'" :visible.sync="show">
      <el-form :model="items">
        <el-form-item label="小区名称" label-width="100px">
          <el-input v-model="items.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="所属地区" label-width="100px">
          <!-- 省 -->
          <el-select v-model="items.province" placeholder="请选择" @change="changeProvince">
            <el-option
              v-for="item in areas"
              :key="item.id"
              :label="item.cityName"
              :value="item.cityName">
            </el-option>
          </el-select>
          <!-- 市 -->
          <el-select v-model="items.citys" placeholder="请选择" @change="changeCity">
            <el-option
              v-for="item in city"
              :key="item.id"
              :label="item.cityName"
              :value="item.cityName">
            </el-option>
          </el-select>
          <!-- 区 -->
          <el-select v-model="items.regions" placeholder="请选择">
            <el-option
              v-for="item in region"
              :key="item.id"
              :label="item.cityName"
              :value="item.cityName">
            </el-option>
          </el-select>
          <!-- 详细地址 -->
          <el-input v-model="items.adress" autocomplete="off" placeholder="请填写详细地址"></el-input>
        </el-form-item>
        <el-form-item label="小区坐标" label-width="100px">
          <el-button @click="getPosition">获取坐标</el-button>
          {{ items.position.length > 0 ?items.position.join(','):'' }}
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="show = false">取 消</el-button>
        <el-button type="primary" @click="show = false;addQuarterRow();addEditQuarter()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import axios from 'axios';
// 导入所有城市数据
import area from '../utils/area.json'

export default {
  name:'Quarters',
  data() {
    return {
      // 保存搜素框的值
      quarter: {
        name: '', // 小区名字
        createTime:[] // 开始和结束事件
        
      },
      tableData:[],
      dataCount:0, // 数据总数
      currentPage:1, // 当前页
      startTime:'', // 开始时间
      endTime:'',  // 结束时间 
      show:false, // 控制对话框 弹出和隐藏
      items:{
        name:'', // 小区名字
        citys:'', // 省
        province:'', // 市
        regions:'', // 区
        adress:'', // 详细地址
        position:[] // 经纬度
      },
      areas:area, //所有城市信息
      city:'' ,   // 所有市的信息
      region:'',   // 所有市下的区的信息
      title:false, // 控制对话框的名字
      obj:{}  // 请求数据传入的参数
    }
  },
  methods: {
    // 
    async getDate(){
      let res = await axios.get(`http://43.143.190.87:8091/panghudaojiaBackend/community/communityList?name=&startTime=${this.startTime}&endTime=${this.endTime}&pageNum=${this.currentPage}&pageSize=10&communityName=`+this.quarter.name)
      // console.log(res.data.data);
      this.tableData = res.data.data.list
      this.dataCount = res.data.data.total
      // console.log(res);
      
    },
    search(){
      // console.log(1);
      
      console.log(this.quarter);
      // 判断 小区名称 name 是否为空
      if(this.quarter.name.trim() == '' && !this.quarter.createTime){
        this.$message({
          message:'查询条件不能为空',
          type:'warning',
          duration:1000
        })
        return
      }
      // 判断日期是否选择
      // if(){
      //   this.$message({
      //     message:'请选择创建时间',
      //     type:'warning',
      //     duration:1000
      //   })
      //   return
      // }
      // console.log(this.quarter.createTime);
      
      if(this.quarter.createTime){
        // 对象
        let startTime = this.quarter.createTime[0]
        let endTime = this.quarter.createTime[1]
        // 把对象转化为 年-月-日 时:分:秒

        var y = startTime.getFullYear();   // 获取年
        var m = startTime.getMonth() + 1;  // 获取月
        m = m < 10 ? ('0' + m) : m;        // 小于10的月份补充0
        var d = startTime.getDate();       // 获取一个月已经过的天数
        d = d < 10 ? ('0' + d) : d;        // 小于10的天补充0
        var h = startTime.getHours();      // 获取小时
        h = h < 10 ? ('0' + h) : h;
        var minute = startTime.getMinutes(); // 获取分钟
        minute = minute < 10 ? ('0' + minute) : minute; // 小于10的分钟补充0
        startTime = y + '-' + m + '-' + d+'+'+h+':'+minute+":00"; 


        var y = endTime.getFullYear();
        var m = endTime.getMonth() + 1;
        m = m < 10 ? ('0' + m) : m;
        var d = endTime.getDate();
        d = d < 10 ? ('0' + d) : d;
        var h = endTime.getHours();
        h = h < 10 ? ('0' + h) : h;
        var minute = endTime.getMinutes();
        minute = minute < 10 ? ('0' + minute) : minute;
        endTime = y + '-' + m + '-' + d+'+'+h+':'+minute+":00"; 

        // console.log(startTime, endTime);

        this.startTime = startTime
        this.endTime = endTime
      }else{
        // 重置时间
        this.startTime = ''
        this.endTime = ''
      }
      
      // 发起网路请求
      this.getDate()

    },
    addQuarter(){
      this.show = true

    },
    // 添加新一行小区
    async addQuarterRow(){
      // console.log(1);
      if(!this.title){
        if(!this.items.citys ||!this.items.province||!this.items.regions || !this.items.adress || !this.items.position ){
        this.$message({
          message:'请填写完整信息',
          type:'warning',
          duration:1000
        })
        this.show = true
        return
      }
      this.obj = {
        addOrUpdateFlag:1,
        communityAddress:this.items.adress,
        communityAreaCode:'',
        communityCity:this.items.citys,
        communityCoordinates:this.items.province+this.items.citys+this.items.regions,
        communityDistrict:this.items.regions,
        communityId:'',
        communityLatitude:this.items.position.length>0 ? this.items.position[0]:'',
        communityLongitude:this.items.position.length>0 ? this.items.position[1]:'',
        communityName:this.items.name,
        communityProvince:this.items.province
      }
      let res = await axios.post(`http://43.143.190.87:8091/panghudaojiaBackend/community/communityUpdate`,this.obj)
      // console.log(res);

      if(res.data.status == 200){
        this.$message({
          message:'添加成功',
          type:'success',
          duration:1000,
          onClose:() => {
            this.show = false
            this.getDate()
            // console.log(this.tableData);
            
          }
        })
        
      }
      }

      
      
    },
    showStores(data){

    },
    editQuarter(data){
      this.show = true
      this.title = true
      // console.log(data);

      // 渲染弹出的对话框
      this.items.name = data.communityName
      this.items.province = data.communityProvince
      this.items.citys = data.communityCity
      this.items.regions = data.communityDistrict
      this.items.adress = data.communityAddress
      this.items.position[0] = data.communityLatitude
      this.items.position[1] = data.communityLongitude

      console.log(this.items.name);
      
    },
    addEditQuarter(){
      // this.obj = {
      //   addOrUpdateFlag:1,
      //   communityAddress:this.items.adress,
      //   communityAreaCode:'',
      //   communityCity:this.items.citys,
      //   communityCoordinates:this.items.province+this.items.citys+this.items.regions,
      //   communityDistrict:this.items.regions,
      //   communityId:'',
      //   communityLatitude:this.items.position.length>0 ? this.items.position[0]:'',
      //   communityLongitude:this.items.position.length>0 ? this.items.position[1]:'',
      //   communityName:this.items.name,
      //   communityProvince:this.items.province
      // }
      if(this.title){
        data.communityName =  this.items.name
        data.communityProvince=  this.items.province 
        data.communityCity = this.items.citys  
        data.communityDistrict= this.items.regions 
        data.communityAddress = this.items.adress 
        data.communityLatitude=this.items.position[0] 
        data.communityLongitude= this.items.position[1] 
      }
      
      
      
      // console.log(this.items.name);
      
    },
    del(data){
      // console.log(data);
      this.$confirm('此操作将会删除该信息, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        // 发起删除网络请求
        let res = await axios.post('http://43.143.190.87:8091/panghudaojiaBackend/community/communityRemove',{"communityId": data.communityId}
        )
        
        
        // console.log(res);
        // 判断是否删除成功
        if(res.status == 200){
          this.$message({
            type:'success',
            message:'删除成功',
            duration:1000,
            onClose:() => {
              // 渲染之前判断是否是最后一页最后一条数据，是的话需要自动向前翻页
              if(this.tableData.length == 1){
                this.currentPage--
              }
              this.getDate()
              // console.log(this.tableData);
              
              // if(this.tableData.length === 0){
              //   this.currentPage--
              //   this.getDate()
              // }
              // bug
              // 4.5 删除最后一页的最后一条，需要自动向前翻页
            }
            // onClose(){
            //   this.getDate()
            // }
          })
        }else{
          this.$message({
            type:'error',
            message:'删除失败'
          })
        }
        
        this.$message({
          type: 'success',
          message: '删除成功!'
        });
      }).catch(() => {
              
      });

    },
    changePage(pageNum){
      // 切换分页
      this.currentPage = pageNum
      this.getDate()
    },
    // 省下拉选择改变时触发
    changeProvince(val){
      // console.log(val);
      for(let i = 0; i < this.areas.length;i++){
        if(this.areas[i].cityName == val){
          this.city = this.areas[i].provincesCityList
          break
        }
      }
      this.items.citys = ''
      this.items.regions = ''
      this.region = []
      
      // console.log(this.city);
      
    },
    // 市下拉选择改变时触发

    changeCity(val){
      for(let i = 0; i < this.city.length;i++){
        if(this.city[i].cityName == val){
          this.region = this.city[i].provincesCityList
          break
        }
      }
      // console.log(this.items.citys);
      this.items.regions = ''

      
    },
    // 获取经纬度
    async getPosition(){
      if(!this.items.province){
        this.$message({
          message:'请输入所属省份',
          type:'warning',
          duration:1000
        })
        return
      }
      if(!this.items.citys){
        this.$message({
          message:'请输入所属城市',
          type:'warning',
          duration:1000
        })
        return
      }
      if(!this.items.regions){
        this.$message({
          message:'请输入所属区县',
          type:'warning',
          duration:1000
        })
        return
      }
      if(!this.items.adress){
        this.$message({
          message:'请输入详细地址',
          type:'warning',
          duration:1000
        })
        return
      }
      let address = this.items.province+this.items.citys+this.items.regions+this.items.adress
      
      let res = await axios.get(`https://restapi.amap.com/v3/geocode/geo?key=457886b5972a97e93fb9cdccebfd450f&address=${address}`)
      // console.log(res);
      let location = res.data.geocodes[0].location
      this.items.position = location.split(',').reverse()
      // console.log(this.items.position);
      
      
    }
    
    
  },
  mounted() {
    // 第一次页面打开时调用
    this.getDate()
    
  },
}
</script>

<style>
.el-table__cell{
  text-align: center !important;
}
th.el-table__cell{
  background-color: #1890ff !important;
  color: #fff;
}
.el-select{
  width: 190px !important;
  margin-right: 15px;
}
.el-dialog .el-input__inner{
  margin-top: 15px !important;
}
</style>